<template>
  <v-flex xs12 :class="classes">
    <v-card data-aos="fade-left" data-aos-delay="600">
      <v-card flat tile height="350" color="grey lighten-1" dark>
        <v-img :src="value.image" height="100%" gradient="rgba(0, 0, 0, .42), rgba(0, 0, 0, .42)">
          <v-flex xs12>
            <v-chip
              label
              class="mx-0 mb-2 text-uppercase"
              color="grey darken-3"
              text-color="white"
              small
            >{{ value.name }}</v-chip>
          </v-flex>
        </v-img>
      </v-card>
      <v-card-text>
        <div v-html="value.desc"></div>
      </v-card-text>
    </v-card>
  </v-flex>
</template>
<script>
export default {
  overlay: false,
  inheritAttrs: false,
  props: {
    size: {
      type: Number,
      required: true
    },
    value: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    classes() {
      return {
        md6: this.size === 2,
        md4: this.size === 3
      };
    }
  }
};
</script>

<style>
.v-image__image {
  transition: 0.3s linear;
}
</style>